
@keyframes slideIn {
    from {
        transform: translateY(-100%);
    }

    to {
        transform: translateY(0);
    }
}

@keyframes slideOut {
    from {
        transform: translateY(0);
    }

    to {
        transform: translateY(-100%);
    }
}

.modal.slideIn {
    animation: slideIn 0.3s ease-in-out;
}

.modal.slideOut {
    animation: slideOut 0.3s ease-in-out;
}

.header__button_burger{
    width: 100%;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
    background-color:cover;
    padding: 20px;
    animation: slideIn 0.3s ease-in-out;
    z-index: 9999
    
    
}
.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: inherit;
    filter: blur(10px);
    z-index: 9998;
    
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  background-color: black;
  
}


.modal__burger{
    padding: 0;
    background-color: transparent;
    fill:white;
    border:none;
    @media screen and (min-width: 1440px) {
            display: none;
        }
}


