  .bgContainer {
    position: relative;
    width: 100%;
    min-height: 100vh;
    /* display: flex;
    flex-direction: column;
    justify-content: space-between; */
    margin: 0 auto; 
    box-sizing: border-box;
    background-color: #0a0a11;
    color: #f3f3f3;
    overflow: hidden;
  } 
  
  .gradient {
    position: absolute;
    top: 5.5em;
    left: 0;
    width: 387px;
    height: 381px;
    flex-shrink: 0;
    /* border-radius: 387px; */
    background: rgba(188, 230, 210, 0.4);
    filter: blur(104.8543701171875px); 
  } 

  .ellipse1 {
    position: absolute;
    left: -80%;
    top: 1.25em;
    width: 520px;
    height: 550px;
    flex-shrink: 0;
    border-radius: 550px;
    background: rgba(64, 112, 205, 0.5);
    filter: blur(104.8543701171875px);
  } 
.ellipse2 {
    display: none;
  }
  .ellipse3 {
    display: none;
  } 
