.modal {
    background-color: var(--secondary-bg-blue-color);
        padding: 20px;
        border-radius: 8px;
        box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        width: 335px;
        height: 345px;
        position: relative;
        @media screen and (min-width: 768px) {
        width: 450px;
            }
         
}

.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
}
.modal__info_input {
    border-radius: 200px;
    border: 1px solid var(--primary-text-color);
    opacity: 80%;
    width: 285px;
    height: 54px;
    padding-left: 49px;
    margin-top: 20px;
    background-color: transparent;
    color: var(--primary-text-color);
    @media screen and (min-width: 768px) {
            width: 400px;
        }
    
}

.modal__info_addimg{
    display: none;
}

.modal__info_img_modal{
    border-radius:200px;
    width: 100px;
    height: 100px;
   
}

.modal__info_button_add{
    position: absolute;
    top:30%;
    left: 45%;
}
.modal__info_form{
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.modal__info__save{
    width: 285px;
    height: 54px;
    border-radius: 42px;
    padding: 18px 90px;
    color:var(#161f37);
    border: 1px solid gray;
    background-color: var(#F3F3F3);
    @media screen and (min-width: 768px) {
            width: 400px;
        }
}
